<template>
  <el-container class="container">
    <el-header>
      <h1 class="container-title">oldzhao's blog</h1>
      <div class="container-portrait">
        <el-dropdown @command="portraitCommand">
          <span class="el-dropdown-link">
            <img class="headerPic" :src="headerImg" alt="header" v-show="headerImg">
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="logout">退出登录</el-dropdown-item>
            <el-dropdown-item command="personal">个人中心</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>
    <el-container>
      <el-aside width="200px">
        <el-scrollbar>
          <el-menu
            :default-active="activeIndex"
            background-color="#393d49"
            text-color="#fff"
            @open="openNavMenu"
            :default-openeds="defaultOpeneds"
          >
            <!-- <el-submenu index="home">
              <template slot="title">
                <i class="el-icon-location"></i>
                <span>导航一</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="article" @click="turnPage('article')">文章</el-menu-item>
                <el-menu-item index="organization" @click="turnPage('organization')">组织机构</el-menu-item>
              </el-menu-item-group>
            </el-submenu>-->
            <el-menu-item index="article">
              <i class="el-icon-menu"></i>
              <span slot="title">文章</span>
            </el-menu-item>
          </el-menu>
        </el-scrollbar>
      </el-aside>
      <el-container>
        <el-main>
          <div class="container-main">
            <router-view></router-view>
          </div>
        </el-main>
        <el-footer style="height:40px">oldzhao</el-footer>
      </el-container>
    </el-container>

    <el-dialog
      title="登录"
      :visible.sync="dialogVisible"
      width="340px"
      :close-on-click-modal="false"
      :show-close="false"
    >
      <el-row class="container-register">
        <el-col :span="24">
          <el-input v-model="account" placeholder="请输入账号" size="medium"></el-input>
        </el-col>
        <el-col :span="24">
          <el-input v-model="password" placeholder="请输入密码" size="medium" type="password"></el-input>
        </el-col>
        <el-col :span="24">
          <el-button
            type="primary"
            size="medium"
            class="container-register-btn"
            @click="loginInfo"
          >登录</el-button>
        </el-col>
      </el-row>
      <div class="container-login">
        <span class="container-login-title">第三方登录：</span>
        <img src="https://b-gold-cdn.xitu.io/v3/static/img/github.547dd8a.svg" alt="github">
      </div>
    </el-dialog>
  </el-container>
</template>

<script>
import { getCookie } from "@/utils/cookies";
import { loginUser, logout } from "@/api/user";
import { filterObj } from "@/utils/filter";

export default {
  name: "container",
  data() {
    return {
      headerImg: require("../assets/logo.png"),
      defaultOpeneds: [],
      activeIndex: "article",

      dialogVisible: false,
      account: "",
      password: ""
    };
  },
  methods: {
    loginInfo() {
      let data = {
        account: this.account,
        password: this.password
      };

      if (!this.account) {
        this.$message.error("请输入账号!");

        return false;
      }

      if (!this.password) {
        this.$message.error("请输入密码!");

        return false;
      }

      loginUser(filterObj(data)).then(res => {
        if (res.data.ret == 1) {
          this.$message.success("登录成功");

          this.dialogVisible = false;
          this.examineCookies();
        } else if (res.data.ret == 0) {
          this.$message.error(res.data.msg);
        }
      });
    },

    turnPage(path) {
      this.$router.push(path);
    },
    openNavMenu(index) {
      this.defaultOpeneds = [index];
    },
    portraitCommand(command) {
      if (command == "index") {
        // let token = getCookie("token");
        // let uri = this.$store.state.url;
        // removeCookie("token");
        // removeCookie("userInfo");
        // window.location.href =
        //   this.$store.state.logoutUrl + "?token=" + token + "&uri=" + uri;
      } else if (command == "logout") {
        logout().then(res => {
          if(res.data.ret == 1) {
            this.$message.success('已登出！');
            this.account = "";
            this.password = "";
            this.examineCookies();
          }
        })
      }
    },
    examineCookies() {
      if (!getCookie("userInfo")) {
        this.dialogVisible = true;
      } else {
        const userInfo = getCookie("userInfo") ? JSON.parse(decodeURIComponent(getCookie("userInfo"))) : {};

        this.headerImg = userInfo.imgId ? "http://localhost:3000/imgUp/file?id=" + userInfo.imgId : require("@/assets/logo.png");

        this.dialogVisible = false;
      }
    }
  },
  mounted() {
    this.examineCookies();
    this.activeIndex = this.$route.name;
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.el-container.container {
  width: 100%;
  height: 100%;
  background: #393d49;
  overflow: hidden;
}

.container .el-main {
  width: 100%;
  height: calc(100% - 120px);
  padding: 10px;
  background: #f6f7f9;
}

.container .el-main .container-main {
  width: calc(100% - 40px);
  height: calc(100% - 20px);
  background: white;
  padding: 10px 20px;
}

/* 头部 */
.container .el-header {
  background: #393d49;
}

.container .container-title {
  line-height: 60px;
  height: 60px;
  color: #11899f;
  float: left;
}

.container .container-portrait {
  float: right;
}

.container .el-dropdown-link {
  height: 60px;
  line-height: 60px;
}

.container .el-dropdown-link img {
  display: block;
  width: 46px;
  height: 46px;
  margin-top: 7px;
  border-radius: 100%;
}

/* 侧边栏 */
.container .el-scrollbar {
  height: 100%;
}

.container .el-scrollbar__wrap {
  overflow-x: hidden;
}

.container .el-menu {
  border: none;
}

.container .el-menu-item-group__title {
  padding: 0;
}

/* footer */
.container .el-footer {
  background-color: #f6f7f9;
  color: #393d49;
  text-align: center;
  line-height: 40px;
  font-size: 12px;
}

.container .el-menu-item i,
.container .el-submenu__title i {
  color: white;
}

.container .el-submenu .el-menu li.is-active,
.container .el-menu-item.is-active {
  background-color: #11899f !important;
  color: rgb(255, 255, 255);
}

.container-register {
  padding: 10px;
}

.container-register .el-col {
  margin-bottom: 20px;
}

.container-register .container-register-btn {
  width: 100%;
}

.container-login {
  height: 24px;
  line-height: 24px;
  font-size: 14px;
  color: #a2a2a2;
}

.container-login img,
.container-login .container-login-title {
  display: inline-block;
  vertical-align: middle;
}

.container-login img {
  width: 24px;
  margin-left: 10px;
  cursor: pointer;
}
</style>
